.testimonials-section {
  padding: 80px 20px;
  text-align: center;
  background-color: #e8f5e9; /* Doğaya uygun, soft yeşil */
  margin-bottom: 40px;
  border-radius: 25px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.05);
}

.testimonials-section h2 {
  font-family: 'Poppins', sans-serif;
  font-size: 3.5rem;
  margin-bottom: 50px;
  color: #2c3e50;
  position: relative;
  display: inline-block;
  font-weight: 700;
}

.testimonials-section h2::after {
  content: '';
  position: absolute;
  left: 50%;
  bottom: -15px;
  transform: translateX(-50%);
  width: 100px;
  height: 5px;
  background-color: #66bb6a; /* Daha canlı yeşil vurgu */
  border-radius: 3px;
}

.testimonials-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 30px;
  margin-top: 50px;
}

.testimonial-card {
  background-color: #ffffff;
  padding: 30px;
  border-radius: 25px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.testimonial-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.2);
}

.testimonial-image {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  object-fit: cover;
  margin-bottom: 20px;
  border: 4px solid #66bb6a;
}

.testimonial-comment {
  font-family: 'Inter', sans-serif;
  font-size: 1.2rem;
  line-height: 1.8;
  color: #444;
  font-style: normal;
  margin-bottom: 20px;
}

.testimonial-name {
  font-family: 'Inter', sans-serif;
  font-size: 1.1rem;
  font-weight: 600;
  color: #2c3e50;
  margin: 0;
}
