body {
  margin: 0;
  font-family: 'Inter', sans-serif; /* Genel font */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f8f8f8;
  color: #333;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Poppins', sans-serif; /* Başlık fontu */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Global styles for links and buttons */
a {
  color: inherit;
  text-decoration: none;
}

button,
.btn-primary,
.btn-secondary {
  cursor: pointer;
  border: none;
  transition: background-color 0.3s ease;
}

/* Basic reset for lists */
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

/* Basic reset for headings */
h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 0.5em;
  line-height: 1.2;
}

p {
  margin-top: 0;
  margin-bottom: 1em;
}
