.landing-page-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: linear-gradient(to bottom right, #a8e063, #56ab2f); /* Doğal yeşil tonları */
  color: #fff;
  text-align: center;
  overflow: hidden;
  position: relative;
}

.landing-page-content {
  z-index: 1;
  padding: 20px;
  background-color: rgba(0, 0, 0, 0.3); /* Hafif şeffaf arka plan */
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
}

.landing-page-logo {
  width: 250px; /* Logonun boyutu */
  height: auto;
  margin-bottom: 30px;
  filter: drop-shadow(0 0 10px rgba(255, 255, 255, 0.5));
}

.landing-page-content h1 {
  font-family: 'Poppins', sans-serif;
  font-size: 3.5rem;
  margin-bottom: 15px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.landing-page-content p {
  font-family: 'Inter', sans-serif;
  font-size: 1.5rem;
  margin-bottom: 40px;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.4);
}

.enter-button {
  background-color: #fff;
  color: #56ab2f; /* Yeşil buton metni */
  padding: 15px 40px;
  border: none;
  border-radius: 50px;
  font-size: 1.5rem;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

.enter-button:hover {
  background-color: #e0e0e0;
  transform: translateY(-5px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3);
}
