/* Genel Stil Ayarları */
body {
  font-family: 'Inter', sans-serif;
  line-height: 1.6;
  color: #34495e; /* Koyu gri-mavi */
  background-color: #f8f9fa; /* Açık gri */
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

/* Hero Section */
.hero-section {
  background: linear-gradient(to right, #4facfe, #00f2fe), url('https://via.placeholder.com/1500x800?text=Demre+Manzarası+Yeni') no-repeat center center/cover;
  color: #fff;
  text-align: center;
  padding: 150px 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 80vh;
  position: relative;
  overflow: hidden;
}

.hero-content {
  max-width: 900px;
  padding: 40px;
  border-radius: 25px;
  background-color: rgba(0, 0, 0, 0.4);
  backdrop-filter: blur(8px);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.4);
}

.hero-content h1 {
  font-family: 'Poppins', sans-serif;
  font-size: 4.8rem;
  margin-bottom: 30px;
  line-height: 1.1;
  text-shadow: 4px 4px 8px rgba(0, 0, 0, 0.9);
  font-weight: 800;
}

.hero-content p {
  font-family: 'Inter', sans-serif;
  font-size: 1.9rem;
  margin-bottom: 60px;
  text-shadow: 3px 3px 6px rgba(0, 0, 0, 0.8);
  font-weight: 400;
}

.btn-primary {
  background-color: #007bff; /* Canlı Deniz Mavisi */
  color: #fff;
  padding: 22px 45px;
  text-decoration: none;
  border-radius: 50px;
  font-size: 1.5rem;
  font-weight: 700;
  transition: background-color 0.3s ease, transform 0.2s ease, box-shadow 0.3s ease;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3);
}

.btn-primary:hover {
  background-color: #0056b3;
  transform: translateY(-7px);
  box-shadow: 0 12px 25px rgba(0, 0, 0, 0.4);
}

.whatsapp-hero-btn {
  background-color: #25d366; /* WhatsApp yeşili */
  display: inline-flex;
  align-items: center;
  gap: 10px;
}

.whatsapp-hero-btn:hover {
  background-color: #1da851;
}

/* Ortak Bölüm Stilleri */
.about-section,
.features-section,
.demre-intro-section {
  padding: 100px 20px;
  text-align: center;
  background-color: #ffffff; /* Beyaz arka plan */
  margin-bottom: 40px;
  border-radius: 25px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  position: relative; /* Hologram efekti için */
  overflow: hidden; /* Taşmaları engellemek için */
}

.about-section h2,
.features-section h2,
.demre-intro-section h2 {
  font-family: 'Poppins', sans-serif;
  font-size: 4.2rem;
  margin-bottom: 50px;
  color: #e67e22; /* Turuncu tonu */
  position: relative;
  display: inline-block;
  font-weight: 800;
  z-index: 1;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.about-section h2::after,
.features-section h2::after,
.demre-intro-section h2::after {
  content: '';
  position: absolute;
  left: 50%;
  bottom: -18px;
  transform: translateX(-50%);
  width: 120px;
  height: 6px;
  background-color: #007bff;
  border-radius: 4px;
}

.about-section p {
  font-family: 'Inter', sans-serif;
  font-size: 1.4rem;
  line-height: 2;
  max-width: 900px;
  margin: 0 auto 50px auto;
  color: #555;
  position: relative;
  z-index: 1;
  text-shadow: none;
  background-color: transparent;
  padding: 0;
  border-radius: 0;
}

/* Demre Galeri */
.demre-gallery {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 35px;
  margin-top: 60px;
}

.gallery-item {
  background-color: #f0f3f4;
  border-radius: 18px;
  overflow: hidden;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
  transition: transform 0.4s ease, box-shadow 0.4s ease;
}

.gallery-item:hover {
  transform: translateY(-12px) scale(1.03);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.25);
}

.gallery-item img {
  width: 100%;
  height: 250px;
  object-fit: cover;
  display: block;
}

.gallery-item p {
  padding: 25px;
  font-size: 1.3rem;
  font-weight: 700;
  color: #2c3e50;
  margin: 0;
}

/* Özellikler Bölümü */
.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 35px;
  margin-top: 60px;
}

.feature-item {
  background-color: #f0f3f4;
  padding: 40px;
  border-radius: 18px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.15);
  text-align: center;
  transition: transform 0.4s ease, box-shadow 0.4s ease;
}

.feature-item:hover {
  transform: translateY(-12px);
  box-shadow: 0 18px 40px rgba(0, 0, 0, 0.25);
}

.feature-item h3 {
  font-family: 'Poppins', sans-serif;
  font-size: 2.4rem;
  color: #007bff;
  margin-bottom: 25px;
  font-weight: 700;
}

.feature-item p {
  font-family: 'Inter', sans-serif;
  font-size: 1.25rem;
  line-height: 1.8;
  color: #666;
}

.feature-icon {
  font-size: 3rem;
  color: #007bff;
  margin-bottom: 15px;
}

/* Responsive Ayarlamalar */
@media (max-width: 768px) {
  .hero-section {
    padding: 100px 20px;
    min-height: 60vh;
  }

  .hero-content h1 {
    font-size: 3.5rem;
  }

  .hero-content p {
    font-size: 1.5rem;
  }

  .btn-primary {
    padding: 18px 35px;
    font-size: 1.3rem;
  }

  .about-section, .features-section, .demre-intro-section {
    padding: 80px 15px;
  }

  .about-section h2, .features-section h2, .demre-intro-section h2 {
    font-size: 3.2rem;
    margin-bottom: 40px;
  }

  .about-section p {
    font-size: 1.2rem;
    margin-bottom: 40px;
  }

  .demre-gallery,
  .features-grid {
    grid-template-columns: 1fr;
  }

  .gallery-item img {
    height: 200px;
  }

  .gallery-item p {
    font-size: 1.2rem;
  }

  .feature-item {
    padding: 30px;
  }

  .feature-item h3 {
    font-size: 2rem;
  }

  .feature-item p {
    font-size: 1.1rem;
  }
}

@media (max-width: 480px) {
  .hero-section {
    padding: 80px 15px;
    min-height: 50vh;
  }

  .hero-content h1 {
    font-size: 2.5rem;
  }

  .hero-content p {
    font-size: 1.2rem;
    margin-bottom: 40px;
  }

  .btn-primary {
    padding: 15px 30px;
    font-size: 1.1rem;
  }

  .about-section, .features-section, .demre-intro-section {
    padding: 60px 10px;
  }

  .about-section h2, .features-section h2, .demre-intro-section h2 {
    font-size: 2.5rem;
    margin-bottom: 30px;
  }

  .about-section p {
    font-size: 1rem;
    margin-bottom: 30px;
  }

  .gallery-item img {
    height: 180px;
  }

  .gallery-item p {
    font-size: 1.1rem;
  }

  .feature-item {
    padding: 25px;
  }

  .feature-item h3 {
    font-size: 1.8rem;
  }

  .feature-item p {
    font-size: 1rem;
  }
}
