.photos-container {
  padding: 60px 20px;
  font-family: 'Inter', sans-serif;
  color: #333;
  background-color: #f4f4f4;
  text-align: center;
}

.photos-container h2 {
  font-family: 'Poppins', sans-serif;
  font-size: 2.5rem;
  margin-bottom: 20px;
  color: #2c3e50;
}

.photos-container p {
  font-family: 'Inter', sans-serif;
  font-size: 1.1rem;
  color: #555;
  margin-bottom: 40px;
}

.add-photo-btn {
  background-color: #28a745; /* Yeşil buton */
  color: #fff;
  padding: 12px 25px;
  border: none;
  border-radius: 25px;
  font-size: 1.1rem;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  margin-bottom: 30px;
  display: inline-flex;
  align-items: center;
  gap: 10px;
}

.add-photo-btn:hover {
  background-color: #218838;
  transform: translateY(-3px);
}

.add-photo-icon {
  font-size: 1.5rem;
}

.photo-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
}

.photo-item {
  background-color: #fff;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer; /* Add cursor pointer to indicate clickability */
}

.photo-item:hover {
  transform: translateY(-8px);
  box-shadow: 0 12px 25px rgba(0, 0, 0, 0.25);
}

.photo-item img {
  width: 100%;
  height: 280px;
  object-fit: cover;
  display: block;
}

.load-more-btn {
  background-color: #007bff;
  color: #fff;
  padding: 12px 25px;
  border: none;
  border-radius: 25px;
  font-size: 1.1rem;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  margin-top: 40px;
}

.load-more-btn:hover {
  background-color: #0056b3;
  transform: translateY(-3px);
}

/* Image Modal Styles */
.image-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.image-modal-content {
  max-width: 90%;
  max-height: 90%;
}

.image-modal-content img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

/* Responsive Ayarlamalar */
@media (max-width: 768px) {
  .photos-container {
    padding: 40px 15px;
  }

  .photos-container h2 {
    font-size: 2rem;
    margin-bottom: 15px;
  }

  .photos-container p {
    font-size: 1rem;
    margin-bottom: 30px;
  }

  .add-photo-btn {
    padding: 10px 20px;
    font-size: 1rem;
    margin-bottom: 20px;
  }

  .photo-grid {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
  }

  .photo-item img {
    height: 220px;
  }

  .load-more-btn {
    padding: 10px 20px;
    font-size: 1rem;
    margin-top: 30px;
  }
}

@media (max-width: 480px) {
  .photos-container {
    padding: 30px 10px;
  }

  .photos-container h2 {
    font-size: 1.8rem;
    margin-bottom: 10px;
  }

  .photos-container p {
    font-size: 0.9rem;
    margin-bottom: 20px;
  }

  .add-photo-btn {
    padding: 8px 15px;
    font-size: 0.9rem;
    margin-bottom: 15px;
  }

  .photo-grid {
    grid-template-columns: 1fr;
    gap: 15px;
  }

  .photo-item img {
    height: 180px;
  }

  .load-more-btn {
    padding: 8px 15px;
    font-size: 0.9rem;
    margin-top: 20px;
  }
}
